.Question {
    width: 80vw;
    background-color: white;
    text-align: center;
    margin-left: 10vw;
    margin-bottom: 5vh;
    padding-top: 1%;
    padding-bottom: 1%;

}

.title {
    width: 98%;
    background-color: #34a1e5;
    margin-left: 1%;
    height: 40px;
    color: white;
}

input {
    width: 90%;
    height: 40px;
}

.btn {
    height: 40px;
    position: relative;
    top: -2px;
}

.description {
    text-align: left !important;
    margin-left: 10px;
}

.left {
  float: left;
  margin-left: 5px;
}

.right {
  float: right;
  margin-right: 5px;
}



